
*{
   padding: 0;
   margin: 0;
   box-sizing: border-box;
}

.body{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  
}
.rob{
  font-family: 'Roboto', sans-serif;
  color: rgb(255, 43, 78);
}
.container{
  width: 44vw;
  height: 95vh;
 
  margin-bottom: 20px;
  
}

.heading{
  width: 100%;
  height: 15%;
  
  display: flex;
  gap: 22rem;
  align-items:center ;
  padding-left: 12px;
  padding-bottom: 20px;
}

.san{
  font-family: 'Sansita Swashed', cursive;
}
.content{

  width: 100%;
  height: 85%;
 
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding-top: 45px;
  padding-left: 12px;
  margin-top: 15px;
}



.toggl{
  font-size: xx-small;
  cursor: pointer;
  width: 50px;
  height: 19px;
  border: solid 1px rgb(73, 70, 70);
  background-color: rgb(253, 128, 149);
  

}